import React, { useState,  useEffect } from "react";
import todo from "../images/todo.png";
import "../App.css";

//done
const getLocalItmes = () => {
    let list = localStorage.getItem('lists');
    console.log(list);

    if (list) {
        return JSON.parse(localStorage.getItem('lists'));
    } else {
        return [];
    }
}
const Todo = () =>{

    const [inputData, setInputData] = useState('');
    const [items, setItems] = useState(getLocalItmes());
    const [toggle, setToggle] = useState(true);
    const [isEditItem, setIsEditItem] = useState('');

    const addItem = () =>{
        if(!inputData){ //agar input field mein kuch nahi likha wa tou array mein add nahi hoga
                alert('please enter item');
        }else if(inputData && !toggle){
            setItems(
                items.map((elem) =>{
                    if(elem.id === isEditItem){
                        return {...elem, name:inputData}
                    }
                    return elem;
                })
            )
            setInputData('');
            setIsEditItem(null);
            setToggle(true);
        }else{
            const allInputData = {id: new Date().getTime().toString(), name: inputData}
            setItems([...items, allInputData]);
            setInputData(''); //plus button pr click krny k baad input field sy data hat jaega
        }
    }

    //delete single entry
    const deleteItems = (indx) =>{
        const updatedItems = items.filter((elem) =>{
            return elem.id != indx;
        }); 
        setItems(updatedItems);
        
    }   

    //remove all the data
    const removeAll =() =>{
        setItems([]);
    }

    //edit item
    const editItems = (id) =>{
        let newEditItem = items.find((elem) =>{
            return elem.id === id;
        });
        console.log(newEditItem);
        setToggle(false);
        setInputData(newEditItem.name);
        setIsEditItem(id);
    }


        // add data to localStorage
        useEffect(() => {
            localStorage.setItem('lists', JSON.stringify(items))
         }, [items])

    return(
        <>  
            <div className="main-div">
                <div className="child-div">

                <figure>
                    <img src={todo} alt="todologo"/>
                    <figcaption>Add Your List Here</figcaption>
                </figure>
            
                <div className="addItems">
                    <input type="text" placeholder="Add Items.."
                        value={inputData}
                        onChange={(e)=>setInputData(e.target.value)}
                    />
                    {
                        toggle ?  <i className="fa fa-plus add-btn" title="Add Item" onClick={addItem}></i> 
                        : <i className="far fa-edit add-btn" title="Update Item" onClick={addItem}></i>
                    }
            
                </div>

                <div className="showItems">
                {
                    items.map((elem) =>{
                        return(
                        <div className="eachItem" key={elem.id}>
                        <h3>{elem.name}</h3>
                        <div className="todo-btn">
                        <i className="far fa-edit add-btn" title="Edit Item" onClick={() =>editItems(elem.id)}></i>
                        <i className="far fa-trash-alt add-btn" title="Delete Item" onClick={() =>deleteItems(elem.id)}></i>
                        </div>
                    </div>
                     )
                     })
                }
                </div>

                <div className="showItems">
                    {/* <button className="btn effect04" data-sm-link-text="Remove All" onClick={removeAll}><span>Check List</span></button> */}
                    <button className="btn effect04" onClick={removeAll}><span>Remove All</span></button>
                </div>

                </div>
            </div>
        </>

    );
};
export default Todo;